body{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: gainsboro;
  color:#032ca7;
  margin: 0 auto; /* Centering whole content page in center*/
  padding: 0em 10em 20em 10em;
  max-width: 500px;
  font-size: 100%;
  border: 30px outset  #032ca7;
  padding-bottom: 4em;

}

.App-header {
  background-color: #032ca7;
  min-height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: gainsboro;
  margin-top: .5em;
}
.App-link {
  background: none;
}

nav a{
  color:#032ca7;
  margin: 1em;
  padding: 1em;
  font-weight: bold;
}

nav a:visited{
  color: darkviolet;
}
 nav a:hover{
   color: gainsboro;
   background-color: #032ca7;
 }

 main{
   color: #032ca7;

 }
h1{
  color: gainsboro;
}

table{
  border-collapse: collapse;
  display: flex;
  flex-direction: column;
  align-items: center;   
}
caption{
  justify-content: center;
  background-color: #032ca7;
  color: gainsboro;
  padding-left: .75em;
  padding-right: .8em;
  text-align: center;
}
.storelist caption{
  padding-left: 3.85em;
  padding-right: 3.5em;
}

tr, th{
  border: 1px solid #032ca7;
}

tr, td{
  border: 1px solid #032ca7;
  padding: .5em;  
}

tr:nth-child(even){background-color:#032ca7;}
tr:nth-child(even){color:gainsboro;}

.pointer {cursor:crosshair;}

th{
  text-align: center;
}

form{
  padding: 1em;
}
fieldset{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: gainsboro;
  color:#032ca7;
  border: none; 
  padding: 1em; 
}

legend{
  font-size: 1.5em;
  border-color: #032ca7;
  background-color: #032ca7;
  color: gainsboro;

}

label{
  padding-top: 1em;
  font-size: 1.2em; 
}

input{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: #032ca7;
  background-color: gainsboro;
  font-size: 100%;
  width: 25% 
}

button{
padding-left: 2em;
padding-right: 2em;
border-radius: 3px;
font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
font-size: .75em;
background-color: #032ca7;
color: gainsboro;
cursor: pointer;
}

button:hover{
  background-color: gainsboro;
  color: #032ca7;
}

.App {
  text-align: center;
}

.App-logo {
  width: 100px;
  height: 100px;
  pointer-events: none;
  color: #032ca7;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
  
}
